import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';

import './header.css';

export function Header() {
    return (
        <Navbar bg="light" expand="lg" collapseOnSelect={true}>

            <Container>
                <Navbar.Brand href="#home" >
                     <p className={'header-title'}> </p>
                </Navbar.Brand >
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav" role={undefined}>

                    <Nav className="me-auto">
                        <Nav.Link href="#home"> </Nav.Link>

                        <NavDropdown title="" id="basic-nav-dropdown">
                            <NavDropdown.Item href="#accident">   </NavDropdown.Item>
                            <NavDropdown.Item href="#administrative_disputes" onClick={(e) => console.log(
                                'another'
                            )}>
                                - 
                            </NavDropdown.Item>
                            <NavDropdown.Item href="#organizations_support">C </NavDropdown.Item>
                            <NavDropdown.Item href="#land_law"> </NavDropdown.Item>
                            <NavDropdown.Divider />
                            <NavDropdown.Item href="#action/3.4">
                                 
                            </NavDropdown.Item>
                        </NavDropdown>
                        <Nav.Link href="#contact-form"></Nav.Link>
                        <Nav className="ms-auto" id="navbar">
                            <Nav.Link id={'cursor-none'}>.: .  30,</Nav.Link>
                            <div className={'number'}>+7 993 246 03 89</div>
                        </Nav>
                    </Nav>
                </Navbar.Collapse>
            </Container>
        </Navbar>
    );
}

